body {
  position: relative;
  min-width: 320px;
  margin: auto;
  perspective: 1000px;
  background-color: #1C1C1C;
  color: #FFFFFF;
  font-family: "Ubuntu", sans-serif;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #ffcc00 transparent;
}
body::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
body::-webkit-scrollbar-track {
  background: transparent;
}
body::-webkit-scrollbar-thumb {
  background-color: #ffcc00;
  border-radius: 4px;
}
body::-webkit-scrollbar-thumb:hover {
  background: #555;
}

a {
  display: block;
}

h1 {
  font-size: 34px;
  line-height: 140%;
}

p {
  font-size: 18px;
  line-height: 160%;
}

.highlight {
  color: #006fe8;
  font-weight: 700;
}
.highlight span {
  color: #FFFFFF;
  font-weight: 300;
}

.container {
  max-width: 1680px;
  margin: 0 auto;
  width: 87.5%;
}

.logo {
  font-size: 26px;
  font-weight: 700;
  color: #FFFFFF;
  cursor: pointer;
  transition: 0.3s ease-in-out all;
  padding: 10px;
}
.logo span {
  font-weight: 300;
  transition: inherit;
}
.logo:hover span {
  color: #006fe8;
}

.btn {
  padding: 15px 30px;
  font-size: 16px;
  background-color: #006fe8;
  cursor: pointer;
  font-weight: 400;
  border-radius: 25px;
  transition: 0.3s ease-in-out all;
  color: #FFFFFF;
}
.btn:hover {
  background-color: #004a9c;
  border-radius: 10px;
}

.btn-text {
  color: #006fe8;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  transition: 0.3s ease-in-out all;
}
.btn-text::before {
  content: "›";
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #006fe8;
  font-size: 24px;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
  font-weight: 400;
  transition: inherit;
}
.btn-text:hover::before {
  border-radius: 10px;
}

.header {
  padding: 55px 0;
  position: absolute;
  width: 100%;
  z-index: 100;
}
.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 160px 0;
}
.card-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../images/ukraine.svg") no-repeat center/80%;
  opacity: 0.02;
  pointer-events: none;
}
.card-section__container {
  max-width: 1120px;
}
.card-section__row {
  display: flex;
  gap: 6vw;
}
.card-section__descr {
  flex: 1;
}
.card-section__descr h1 {
  margin-bottom: 20px;
}
.card-section__descr p {
  margin-bottom: 15px;
}
.card-section__card {
  transform-style: preserve-3d;
}

.card {
  position: relative;
  height: 288px;
  padding: 40px 45px 32px 50px;
  border-radius: 15px;
  background: linear-gradient(90deg, rgb(0, 111, 232) 0%, rgb(0, 48, 99) 100%);
  max-width: 458px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.card::after {
  content: "";
  position: absolute;
  border-radius: inherit;
  background: inherit;
  top: 15px;
  left: -15px;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  opacity: 0.3;
  transform: translateZ(-50px);
}
.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../images/ukraine.svg") no-repeat center/80%;
  opacity: 0.07;
  pointer-events: none;
}
.card__logo {
  font-weight: 700;
  text-align: right;
  font-size: 20px;
  margin-bottom: 15px;
  transform: translateZ(15px);
}
.card__logo span {
  font-weight: 300;
}
.card__chip {
  width: 70px;
  object-fit: contain;
  margin-bottom: 25px;
}
.card__form {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.card__form-row {
  display: flex;
  align-items: center;
  gap: 15px;
}
.card__input {
  font-size: 18px;
  color: #FFFFFF;
  background-color: transparent;
  border-bottom: 2px solid transparent;
  transition: 0.3s ease-in-out all;
}
.card__input:-webkit-autofill, .card__input:-webkit-autofill:hover, .card__input:-webkit-autofill:focus, .card__input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #FFFFFF !important;
}
.card__input::placeholder {
  color: #999999;
}
.card__input_number, .card__input_cardholder {
  font-size: 20px;
  text-transform: uppercase;
}
.card__input_number {
  width: 196px;
}
.card__input_valid {
  width: 22px;
  text-align: center;
}
.card__input_cvv {
  width: 35px;
  text-align: center;
}
.card__input_cardholder {
  width: 100%;
}
.card__input.error {
  border-bottom: 2px solid #8f0000;
}
.card__input-area {
  display: flex;
  gap: 5px;
}
.card.error {
  animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
}

@media only screen and (max-width: 1025px) {
  .header {
    padding: 30px 0;
  }
  .card-section {
    padding: 100px 0 50px;
  }
  .card-section__row {
    gap: 30px;
  }
  .card-section__card {
    transform-style: flat;
    transform: none !important;
  }
  .card {
    max-width: 350px;
    height: 220px;
    padding: 25px 25px 15px 30px;
  }
  .card::after {
    top: 7px;
    left: -7px;
  }
  .card__logo {
    font-size: 18px;
  }
  .card__chip {
    width: 60px;
    margin-bottom: 15px;
  }
}
@media only screen and (max-width: 769px) {
  h1 {
    font-size: 28px;
  }
  p {
    font-size: 16px;
  }
  .container {
    width: 100%;
    padding: 0 15px;
  }
  .btn {
    padding: 10px 20px;
  }
  .card-section__row {
    flex-direction: column;
  }
  .card-section__card {
    align-self: center;
  }
}